.info-list {
  display: none;

  @media (max-width: $tablet-width) {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    justify-content: flex-start;
    margin: 0;
    padding: 0;
  }
}

.info-list li:not(:last-of-type) {
  @media (max-width: $tablet-width) {
    margin: 0 0 21px;
  }
}

.info-list__link {
  text-decoration: none;

  @media (max-width: $tablet-width) {
    font-size: 18px;
    line-height: 23px;
    color: $white-shadow;
  }

  &:hover,
  &:focus {
    outline: none;
    opacity: 0.7;
  }

  &:active {
    opacity: 0.5;
  }
}
